<template>
    <div class="container mt-5">
        <div class="text-left mb-3">
            <button @click="$router.back()" class="btn btn-success btn-large"><i class="fa fa-chevron-left mr-2"></i> Go Back</button>
        </div>  
        <div class="row align-items-center" v-if="!loading">
            <div class="col-lg-4 mb-5 mb-lg-0">
                <div class="image-wrapper position-relative">
                    <img :src="player.thumbnail" class="w-100"/>
                    <div class="shirt-number">
                        {{ player.number ? player.number : 'N/A'}}
                    </div>
                </div>
            </div>
            <div class="col-lg-8 text-left">
                <h2> {{ player.name }}</h2>
                <h4> <strong> Date of Birth</strong>: {{ new Date(player.birthDate).toLocaleDateString(undefined, { year: "numeric", month: "long", day: "numeric" }) }}</h4>
                <h4> <strong> Position on Field </strong>: {{ player.position }}</h4>
                <h4> <strong> Description: </strong></h4>
                <p> {{ player.description}}</p>
            </div>
        </div>
        <div class="text-center loader" v-else>
            <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style="margin: auto; background: transparent; display: block; shape-rendering: auto;" width="200px" height="200px" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
                <circle cx="50" cy="50" fill="none" stroke="#353535" stroke-width="3" r="35" stroke-dasharray="164.93361431346415 56.97787143782138">
                <animateTransform attributeName="transform" type="rotate" repeatCount="indefinite" dur="1s" values="0 50 50;360 50 50" keyTimes="0;1"></animateTransform>
                </circle>
            </svg>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    data(){
        return {
            player: null,
            loading: true
        }
    },
    mounted(){
        var playerName = this.$route.params.uri;
        this.getPlayerInfo(playerName, this.$route.query.sport);
    },
    methods: {
        async getPlayerInfo(playerName, sport = null){
            await axios
                .get("http://localhost:8080/api/players/q?name=" + playerName + "&sport=" + sport)
                    .then((response) => {
                        this.player = response.data[0];
                        this.loading = false;
                });   
        }
    }
}
</script>

<style>
    .loader {
        min-height: 92vh;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }

    .image-wrapper img {
        max-height: 600px;
        object-fit: cover;
        object-position: top;
    }

    .shirt-number {
        background: #2e1075;
        width: 100px;
        height: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        font-size: 32px;
        font-weight: 900;

        position: absolute;
        top:0;
        left: 0;
    }
</style>