<script>
import axios from "axios";

export default {
  data() {
    return {
      barcelona: null,
      loading: true
    };
  },
  created() {
    this.getBarcelonaClubInfo();
  },
  methods: {
    async getBarcelonaClubInfo() {
      await axios
        .get("http://localhost:8080/api/players/search?player=FC%20Barcelona")
        .then((response) => {
          this.barcelona = response.data;
          this.loading = false;
        });     
    },
  },
};
</script>

