<template>
  <div class="players-section">
    <div class="container">
     <h2>Current Players of FC Barcelona </h2>

    <div class="row mb-5">
      <div class="offset-lg-3 col-lg-4">   <input type="text" class="form-control" id="searchPlayer" placeholder="Search for a player" v-model="searchInput" @keyup.enter="searchPlayer"></div>
       <div class="col-lg-2">     <button class="btn btn-primary" @click="searchPlayer"> Search Players</button></div>
     </div>

    <div class="row" v-if="!loading">
      <div class="col-lg-8">
        <div class="row all-players" v-if="!loading">
          <div class="col-lg-4" v-for="team in team.players" :key="team.fullName">
              <div class="player-wrapper">
                <router-link :to=getPlayerLink(team.name)>
                  <img v-if="team.thumbnail" :src="team.thumbnail" alt="">
                  <img v-else src="https://via.placeholder.com/600x800">
                  <h4>{{ team.name }}</h4>
                </router-link>
                  <router-link :to=getPlayerLink(team.name) class="btn btn-primary">Read More</router-link>
              </div>
          </div>
      </div>
      </div>
      <div class="col-lg-4">
        <div v-if="!loading" class="widgets">
          <div class="widget text-left">
            <i class="far fa-futbol"></i>
            <div class="widget-text">
              <h4>Stadium Name</h4>
              <p class="lead">{{ team.groundName }}</p>
            </div>
          </div>
          <div class="widget text-left">
            <i class="far fa-user-tie"></i>
            <div class="widget-text">
              <h4>Chairman </h4>
              <p class="lead">{{ team.chairmanName }}</p>
            </div>
          </div>
          <div class="widget text-left">
            <i class="far fa-user-tie"></i>
            <div class="widget-text">
              <h4>Manager </h4>
              <p class="lead">{{ team.managerName }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="loader text-center" v-else>
      <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style="margin: auto; background: transparent; display: block; shape-rendering: auto;" width="200px" height="200px" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
                <circle cx="50" cy="50" fill="none" stroke="#353535" stroke-width="3" r="35" stroke-dasharray="164.93361431346415 56.97787143782138">
                <animateTransform attributeName="transform" type="rotate" repeatCount="indefinite" dur="1s" values="0 50 50;360 50 50" keyTimes="0;1"></animateTransform>
                </circle>
            </svg>
    </div>
     
    </div>
  </div>

</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      team: null,
      searchInput: "",
      loading: true,
      filteredPlayers: null
    };
  },
  created() {
    this.getFootballPlayers();
  },
  methods: {
    async getFootballPlayers() {
      await axios
        .get("http://localhost:8080/api/teams/search?team=FC%20Barcelona")
        .then((response) => {
          this.team = response.data;
          this.loading = false;
        });     
    },
    getPlayerLink(player){
      return "/players/" + encodeURIComponent(player.replace('_', '%20')) + "?sport=football";
    },
    async searchPlayer(){
      this.loading = true;
      await axios
        .get("http://localhost:8080/api/players/q?name=" + this.searchInput + "&sport=football")
        .then((response) => {
          this.team.players = response.data;
          this.loading = false;
        });   
    }
  },
};
</script>

